			<div class="app">
				<p class="name">豆瓣 6.0</p>
				<a href="" class="download">下载豆瓣 App</a>
				<!-- 二维码 -->
				<div class="app_qr">
					<a href="" class="qr"><img :src="appPic"></a>
					<div class="expand">
						<img :src="qrPic">
						<p>手机扫码登陆</p>
					</div>
				</div>
			</div>
			<div class="left" :class="tag">
				<span class="one" @click="reg">注册</span>
				<span class="two" @click="sign">密码登录</span>
				<!-- 注册部分 -->
				<div class="register">
					<!-- 手机号 -->
					<div class="zhuce">
						<p class="tips">请仔细阅读</p>
						<a href="">豆瓣使用协议、隐私政策</a>
						<!-- 邮箱框 -->
						<div class="email">
							<input placeholder="邮箱"  v-model="userEmail"/>
						</div>
						<!-- 登录框 -->
						<div class="phone">
							<input placeholder="手机号" v-model="userPhone"/>
							<span class="p_title">+86</span>
						</div>
						<!-- 密码 -->
						<div class="pwd">
							<input type="password" placeholder="密码" v-model="userPwd">
						</div>
					</div>
					<div class="sub">
						<button @click="doRegister()">豆瓣注册</button>
						<!-- <router-link to=''  @click="doRegister()">豆瓣注册</router-link> -->
					</div>
					<div class="third">
						<p>第三方登陆</p>
						<span class="weixin"></span>
						<span class="weibo"></span>
					</div>
				</div>
			</div>
			<div class="password" :class="tag">
				<span class="one" @click="reg">注册</span>
				<span class="two" @click="sign">密码登录</span>
				<div class="register">
					<!-- 手机号 -->
					<div class="zhuce back">
						<a href="">找回密码</a>
						<!-- 登录框 -->
						<div class="phone">
							<input placeholder="手机号" />
						</div>
					</div>
					
					<div class="mima">
						<input type="password" placeholder="密码" />
					</div>
					<div class="ask">
						<p class="haiwai">海外手机登陆</p>
					</div>
					<div class="sub">
						<!-- <a href="">豆瓣登陆</a> -->
						<button>豆瓣登陆</button>
					</div>
					<div class="third">
						<p>第三方登陆</p>
						<span class="weixin"></span>
						<span class="weibo"></span>
					</div>
				</div>
			</div>
		
		</div>
</template>

<script>
	export default{
		data(){
			return {
					appPic:require("../assets/img/icon_qrcode_green.png"),
					qrPic:require("../assets/img/qr.png"),
					tag:{  //切换注册和登陆
						open1:true,
						open2:false
					},
					userEmail:'',
					userPhone:'',
					userPwd:''
				}
			},
		methods:{
			reg(){
				this.tag.open1=true,
				this.tag.open2=false
			},
			sign(){
				this.tag.open2=true,
				this.tag.open1=false
			},
			doRegister(){
				if(this.userEmail!=''){
					// 将用户的输入数据提交给服务器端API（发起POST请求）
						let url='http://lsbdouban.applinzi.com/user/register'
						// (POST请求中body必须是键值对的形式)
						let body=`email=${this.userEmail}&phone=${this.userPhone}&pwd=${this.userPwd}`
						
						// 发起POST请求 fetch(url,{}).then().then()
						fetch(url,{
							// 这里如果不给选项参数，则默认发送GET请求
							method:'POST',		//指定此次请求的方法
							body:body,		//请求主体
							// 重要(前端发请求必须使用的):修改请求消息的头部，告诉服务器此次请求主体的内容类型
							headers:{'Content-Type':'application/x-www-form-urlencoded'}
						}).then(res=>{
							return res.json()  //json解析一下
						}).then(data=>{
						confirm("注册成功!")
						this.userEmail='',
						this.userPhone='',
						this.userPwd=''
						})
					}
				else{
					confirm("您还没有输入信息！");
					}
				}
		}
	}
</script>

