import {
	createRouter,
	createWebHistory
} from 'vue-router'
import GroupArea from '../views/GroupArea.vue'
import BookArea from '../views/BookArea.vue'
import HeaderArea from '../views/HeaderArea.vue'
import MovieArea from '../views/MovieArea.vue'
import TimeArea from '../views/TimeArea.vue'

const routes = [
	{
		path: '/time',
		component: TimeArea
	},
	{
		path: '/movie',
		component: MovieArea
	},
	{
		path: '/group',
		component: GroupArea
	},
	{
		path: '/book',
		component: BookArea
	},
	{
		path: '/',
		component: HeaderArea
	}
	// {
	//   path: '/about',
	//   name: 'About',
	//   // route level code-splitting
	//   // this generates a separate chunk (about.[hash].js) for this route
	//   // which is lazy-loaded when the route is visited.
	//   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
	// }
]

const router = createRouter({
	history: createWebHistory(process.env.BASE_URL),
	routes
})

export default router
